import React from "react";
import { Article } from "components";
import interview from "data/community-interview";

const Modal = ({ isOpen, close, data_id }) => {
  return (
    <React.Fragment>
      {isOpen ? (
        <React.Fragment>
          <div className="Modal-overlay" onClick={close} />
          <div className="Modal">
            <div className="content">
              <Article id={data_id} />
            </div>
            <div className="button-wrap">
              <button onClick={close}>Confirm</button>
            </div>
          </div>
        </React.Fragment>
      ) : null}
    </React.Fragment>
  );
};
export default Modal;
