import React, { Component } from "react";
import axios from "axios";

class Article extends Component {
  constructor(props) {
    super(props);
    const { id } = this.props;
    console.log(id);
    this.state = {
      id: id,
    };
  }

  sendResponse = () => {
    //e.preventDefault();
    var config = {
      headers: { "Access-Control-Allow-Origin": "*" },
    };
    axios
      .get(
        //        "https://script.google.com/macros/s/AKfycbyon-RP96q6-kPgxB9qSyEpHCgQ8aG2DxF5wLwZGMJ1wvORw9Q/exec",
        this.state.id,
        { params: this.state },
        config
      )
      .then((res) => {
        console.log(res.data);
        this.setState({ data: res.data.result });
      });
  };

  componentDidMount() {
    this.sendResponse(this.state);
  }

  render() {
    const data = this.state.data;
    if (data !== undefined) {
      return <div>{data[0]}</div>;
    } else {
      return <div></div>;
    }
  }
}

export default Article;
